
#app {
  min-width: 100%;
  height: 100%;
  margin: 0 auto;
  background: #eef1f6;
  font-family: "Helvetica Neue",Helvetica,"PingFang SC","Hiragino Sans GB","Microsoft YaHei","",Arial,sans-serif;
}

/*  */
header {
  z-index: 1000;
  -webkit-transition: all 0.5s ease;
  transition: all 0.5s ease;
  background-color: #337ab7;
  -webkit-box-shadow: 0 2px 4px 0 rgba(0, 0, 0, .12), 0 0 6px 0 rgba(0, 0, 0, .04);
          box-shadow: 0 2px 4px 0 rgba(0, 0, 0, .12), 0 0 6px 0 rgba(0, 0, 0, .04);
  padding-bottom: 3px;
  padding-top: 3px;
}
header .title {
  display: inline-block;
  color: white;
  font-size: 24px;
  margin-top: 14px;
  margin-bottom: 14px;
  margin-left: 20px;
}
header .user {
  display: inline-block;
  color: white;
  font-size: 18px;
  margin-top: 16px;
  margin-right: 20px;
  float: right;
}
.navbar {
  background: #eef1f6;
  height: 100%;
}
body {
  margin: 0;
  background: white;
  height: 93%;
}
.ferrorhead3 {
  width: 298px;
  margin-top: -3px;
  color: #fa5b5b;
  font-size: 12px;
  line-height: 20px;
  vertical-align: top;
  word-break: break-all;
}




